.order-detail-wrap {

	.breadcrumb {
		margin-top: -20px;
	}

	.page-aside {

		.img-bg .tag{
			color:#fff;
			display:inline-block;
			font-weight:bold;
			background-color:rgba(51,51,51,.8);
			padding:4px 12px;
			margin-top:15px;
			border-radius:0 2px 2px 0;
		}

		.product-img{
			height:160px;
			border-radius:6px;
		}

		.order-main-title{
			overflow:hidden;
			margin-bottom:15px;

			h2{
				font-size:1.6rem;
				margin-top:10px;
				margin-bottom:0;
			}
		}

		.fill-group{
			font-size:1.4rem;
		}

		.price-detail{
			border-top:1px dashed #eaeaea;
		}
	}

	.board{
		min-height:auto;

		.traveler-con{
			padding:5px 0;
		}
	}

	.order-cta {
		.page-head{
			margin-top: -10px;
			padding-bottom: 10px;
		}

		.flash-tag{
			margin-bottom:10px;
		}

		h1{
			font-size:1.8rem;
			margin-top:0;
		}
	}

	.order-status {
		.label {
			float: left;
			margin: 1px 10px 0 0;
		}

		p{
			margin-bottom: 0;
			overflow: hidden;
		}
	}
}



.price-detail{
	position:relative;
	margin-bottom:0;
	padding-top:15px;

	&:before,
	&:after {
		content:'';
		display: inline-block;
		width:20px;
		height:20px;
		border-radius:100%;
		background-color:#f5f5f5;
		position:absolute;
		top:-10px;
	}

	&:before{
		left:-25px;
	}
	&:after{
		right:-25px;
	}

	hr{
		margin:15px 0;
	}

	.product-pricing h4 {
		line-height: 1;
	}
}


.order-detail-wrap .info-section{
	padding: 10px 0;

	.info-content{
		padding-left:20px;
	}

	.sub-title{
		margin-top: 0;
		margin-bottom:20px;
		font-size:1.6rem;
	}

	.traveler-con{
		padding:5px 0;
	}
}

#cancel-modal {
	.price-detail, .point-detail{
		border:1px solid #eaeaea;
		border-radius:6px;
		margin:20px 0;
		padding:15px;

		hr {
			border-style: dashed;
			position: relative;
			overflow:visible;
			margin: 15px 0;
		}
	}

	.price-detail{
		background-color:#f8f8f8;

		&:before, &:after {
			display: none;
		}
	}

	.cancel-info {
		tr td{
			color:#ccc;
			font-size:1.2rem;
		}

		tr.choose td {
			color: #333;
			font-weight:bold;
			font-size:1.4rem;
		}

		tr.choose i{
			padding:0 10px;
		}
	}
}

/*App聯絡*/
.contact-app .pic, .contact-app .info {
	float: left;
}
.contact-app .pic, .contact-app .pic img {
	width:40px;
}
.contact-app .info {
	padding-left: 10px;
	width:calc(100% - 40px);
	line-height: 20px;

	.text {
		color:#999;
	}
}
